 40px)`);\n\nfunction close() {\n    containerDiv.value?.classList.add(\"disapear\");\n    zindex.removeZIndex(IPage, zindexValue.value);\n    setTimeout(() => page.closePage(props.id), 500);\n}\nfunction openFeedbackDialog () {\n  dialog.showDialog(\n        defineAsyncComponent(() => import(`../../components/dialogs/giveUsFeedbackDialog.vue`)),\n        { closable: true, title: \"Give Us Feedback\", width: \"540px\", maxHeight: \"706px\" },\n    );\n}\nfunction logout() {\n    executor.execute(async () => { await authentication.signOut(); /*close();*/ }, { loading: true });\n}\n\n</script>\n<template>\n    <teleport to=\"body\">\n        <section class=\"rootSection\" :style=\"{ zIndex: zindexValue }\">\n            <section class=\"columnsSection\">\n                <section class=\"contentSection\" :style=\"{ width: contentWidth }\">\n                    <div ref=\"containerDiv\" :style=\"{ zIndex: zindexValue }\">\n                        <div class=\"header\">\n                            <span class=\"prev-button\" @click=\"close\"><i class=\"mdi mdi-arrow-left-box mdi-30px\"\n                                    style=\"font-size: 30px;\"></i></span>\n                            <div class=\"icon-container small\">\n                                <img src='../../../../images/is-bankasi-logo.jpg' alt=\"is-bankasi-logo\" class=\"header-logo\" width=\"16\"\n                                    height=\"16\" />\n                            </div>\n                            <label class=\"app-name\">{{ viewModel.studio.appName }}</label>\n                            <label class=\"app-name\" style=\"margin-left: 12px;\">/</label>\n                            <label class=\"page-name-text\">{{ props.options.name }}</label>\n                            <div style=\"display: flex; margin-left: 1450px;\">\n                                <!-- <i class=\"mdi mdi-account-circle profile-icon\"></i>\n                                <i class=\"mdi mdi-chevron-down chevronIcon\" data-bs-toggle=\"dropdown\"\n                                    aria-expanded=\"false\" data-bs-target=\"#dropdown-menu\" id=\"dropdownMenuButton1\"\n                                    style=\"padding-top: 16px; margin-right: -11px\"></i>\n                                <ul style=\"width: 246px\" class=\"dropdown-menu\" aria-labelledby=\"dropdownMenuButton1\">\n                                    <li>\n                                        <div class=\"menuıtem\" style=\"display: flex; margin: 9px\">\n                                            <i class=\"mdi mdi-account-circle\" style=\"font-size: 25px\"></i>\n                                            <a style=\"margin: 7px\">{{ viewModel.user?.email }}</a>\n                                        </div>\n                                    </li>\n                                    <li>\n                                        <div class=\"menuıtem\" style=\"display: flex; margin: 9px\" @click=\"logout()\">\n                                            <i class=\"mdi mdi-logout\" style=\"font-size: 25px\"></i>\n                                            <a style=\"margin: 7px\">Logout</a>\n                                        </div>\n                                    </li>\n                                </ul>\n                            </div> -->\n                            <div class=\"navbar-icons col-xl-2\">\n                                <div class=\"col-xl-1 p-0 dot-icon\" style=\"display: flex\">\n                                <img src=\"../../../../images/profile-image-large-in-app.svg\" class=\"profile-icon\" style=\"margin-right: 4px;\"  id=\"dropdownMenuButton1\" data-bs-toggle=\"dropdown\" aria-expanded=\"false\" data-bs-target=\"#dropdown-menu\"/>\n                                <i\n                                    class=\"mdi mdi-chevron-down chevronIcon\"\n                                    data-bs-toggle=\"dropdown\"\n                                    aria-expanded=\"false\"\n                                    data-bs-target=\"#dropdown-menu\"\n                                    id=\"dropdownMenuButton1\"\n                                    style=\"padding-top: 10px; margin-right: -11px; font-size: 16px\"\n                                ></i>\n                                <ul style=\"width: 246px\" class=\"dropdown-menu user-info-dropwdown\" aria-labelledby=\"dropdownMenuButton1\">\n                                    <li>\n                                    <div class=\"user-info\">\n                                        <img src=\"../../../../images/profile-image.svg\" style=\"margin-right: 12px;\"/>\n                                        <div style=\"display: grid;\">\n                                        <span class=\"user-name\">{{ user.name + ' ' + user.lastName }}</span>\n                                        <span class=\"user-email\">{{ user.email }}</span>\n                                        </div>\n                                        \n                                    </div>\n                                    </li>\n                                    <li>\n                                    <div class=\"menuıtem\" @click=\"openFeedbackDialog()\">\n                                        <i class=\"mdi mdi-lifebuoy\" style=\"font-size: 20px; margin-right: 8px;\"></i>\n                                        <a>Give Us Feedback</a>\n                                    </div>\n                                    </li>\n                                    <li>\n                                    <div class=\"menuıtem\" @click=\"logout()\">\n                                        <i class=\"mdi mdi-logout\" style=\"font-size: 20px; margin-right: 8px;\"></i>\n                                        <a>Logout</a>\n                                    </div>\n                                    </li>\n                                </ul>\n                                </div>\n         