padding-left: 12px;\n    font-style: normal;\n    font-weight: 500;\n    ;\n    font-family: Roboto;\n    letter-spacing: 0;\n    color: #212121;\n    text-align: left;\n    opacity: 1;\n}\n\n.chevronIcon {\n    cursor: pointer;\n}\n\n.profile-icon {\n    color: #909090;\n}\n\n.user-info-dropwdown {\n  width: 246px;\n  border: 1px solid #E3E3E3;\n  box-shadow: 1px 2px 4px rgba(0, 0, 0, 0.2);\n  border-radius: 8px;\n}\n.menuıtem:hover {\n  background-color: #d5d5d5 !important;\n  cursor: pointer;\n}\n.menuıtem {\n  font-size: 14px;\n  line-height: 16px;\n  color: #212121;\n  display: flex;\n  align-items: center;\n  padding: 8px 12px;\n  cursor: pointer;\n}\n.user-info {\n  display: flex;\n  align-items: center;\n  padding: 10px 12px 20px;\n}\n.user-name {\n  font-weight: 500;\n  font-size: 14px;\n  line-height: 16px;\n  color: #212121;\n  margin-bottom: 8px;\n  overflow: hidden;\n  text-overflow: ellipsis;\n}\n.user-email {\n  font-size: 14px;\n  line-height: 16px;\n  color: #757575;\n  overflow: hidden;\n  text-overflow: ellipsis;\n}\n</style>\n"],"sourceRoot":""}]);
